.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  height: 408px;
  padding: 32px 16px 16px;
  transition-duration: .3s;
  display: flex;
  position: relative;
  overflow: hidden;
}

.card__image {
  background-image: url("imac.202c52ff.jpeg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 160px;
  height: 134px;
  margin-inline: auto;
}

.card__info {
  color: #060b35;
  margin-top: 40px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
}

.card__rate {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.card__rate-stars, .card__rate-count {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  display: flex;
}

.card__price-title {
  color: #616070;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
}

.card__price-value {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 700;
}

.card:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.card:hover .card__info {
  color: #34568b;
}

.card__buy-button {
  background-color: #00acdc;
  border: hidden;
  border-radius: 5px;
  height: 39px;
  margin-top: 16px;
}

.card__buy-button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card__buy-button:hover a {
  color: #00acdc;
}

.card__buy-button-link {
  color: #fff;
  text-transform: uppercase;
  font-weight: 700;
  text-decoration: none;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

body {
  --col-number: 1;
  margin: 0;
  font-family: Roboto, serif;
  font-style: normal;
  font-weight: 500;
}

header {
  z-index: 1;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  position: sticky;
  top: 0;
}

.nav__list {
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__list-item {
  text-transform: uppercase;
  text-align: center;
  display: inline-block;
}

.nav__list-item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  color: #060b35;
  align-content: center;
  height: 60px;
  font-size: 12px;
  text-decoration: none;
  transition-duration: .3s;
  display: inline-block;
}

.nav__link:hover {
  color: #00acdc;
}

a.is-active {
  color: #00acdc;
  position: relative;
}

a.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 10px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.logo {
  width: 40px;
  height: 40px;
  margin-right: 20px;
}

.cards {
  grid-template-columns: repeat(var(--col-number), 200px);
  justify-content: center;
  justify-items: center;
  gap: 46px 48px;
  padding-block: 50px;
  padding-inline: 40px;
  display: grid;
}

@media (width >= 488px) {
  .cards {
    --col-number: 2;
  }
}

@media (width >= 768px) {
  .cards {
    --col-number: 3;
  }
}

@media (width >= 1024px) {
  .cards {
    --col-number: 4;
  }
}
